<template>
  <div>
    <div
      style="width:100%;
        height:38px;
        background:rgba(13,67,111,1);
        box-shadow:0px 2px 10px 0px rgba(0, 0, 0, 0.05);
        font-size:14px;
        font-family:Microsoft YaHei;
        font-weight:400;
        color:rgba(255,255,255,1);
        padding-left:24px;
        line-height:38px;"
    >
      文件管理
    </div>
    <Card style="margin:10px;">
      <Row>
        <Col style="display: flex; justify-content: flex-start;">
          <div style="margin-left:40px;">
            <span style="color:#fff">文件名称：</span>
            <Input placeholder="请输入" v-model="fileName" :style="{width:180*base+'px',height:'32px','border-radius':'2px'}"></Input>
          </div>
          <div style="margin-left:30px;">
            <span style="color:#fff">类别：</span>
            <Select v-model="fileType" clearable  :style="{width:200*base+'px'}">
              <Option v-for="item in fileList" :value="item.metaDataValue" :key="item.metaDataValue">{{ item.metaDataName }}</Option>
            </Select>
          </div>
          <div>
            <span style="margin: 0 30px;">
              <Button type="primary" @click="search">查询</Button>
            </span>
          </div>
        </Col>
      </Row>
    </Card>
    <Card style="margin:10px;">
      <div style="display:flex; justify-content:flex-end;">
        <Button @click="uploadFile" :disabled="isGray" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">上传文件</Button>
      </div>
      <div style="margin-top:10px;">
        <Table :columns="columns1" :data="fileData" :loading="loadingStatus" :height="tabHeight" id="fileTable"></Table>
        <Page 
          :total="totalCount"  
          :current="pageNo" 
          show-total 
          show-elevator 
          show-sizer 
          @on-change="pageChange" 
          @on-page-size-change="sizeChange" 
          placement="top" 
          style="text-align:right;margin:20px 0"
        />
      </div>
    </Card>

    <Modal class-name="vertical-center-modal" v-model="uploadModal" :closable="true" :mask-closable="false" id="uploadModal">
      <p slot="header" style="color:#484f5f;padding-left: 5px;display:flex;justify-content:space-between;align-items:center">
        <span>上传文件</span>
        <!-- <Icon id="uploadShut" type="ios-close-empty" :size="30" @click="cancel" style="cursor: pointer;"></Icon> -->
      </p>
      <div>
        <Row>
          <Col span="24">
            <Form label-position="left" :label-width="90">
              <FormItem label="上传文件：">
                <el-upload ref="upload" :before-upload='beforeUpload' class="upload-demo" drag action="https://jsonplaceholder.typicode.com/posts/">
                  <i class="el-icon-upload" id="uploadIco"></i>
                  <div class="el-upload__text" style="color:#fff">将文件拖到此处，或
                    <em>点击上传</em>
                  </div>
                </el-upload>
                <div>
                  <div style="width:91%;display:flex;justify-content:space-between">
                    <Tooltip placement="top-start">
                      <p style="width:200px;overflow:hidden;white-space: nowrap;text-overflow: ellipsis;color:#fff">{{uploadFileData?uploadFileData.name:''}}</p>
                      <div slot="content">
                        <p style="white-space: normal;word-wrap:break-word">
                          {{uploadFileData?uploadFileData.name:''}}
                        </p>
                      </div>
                    </Tooltip>
                    <p style="color:#f56c6c" v-show="errorProgress">上传失败</p>
                  </div>
                  <div style="display:flex;align-items:center" v-if="successProgress">
                    <el-progress :percentage="processNum" :stroke-width="1" style="width:88%;color:#fff" id="progress"></el-progress>
                    <Button type="text" size="small" @click="cancelUpload" id="cancelUpload" v-show="processNum!==100" style="color:rgb(2, 232, 255)">取消</Button>
                  </div>
                </div>
                <div v-if="errorProgress">
                  <el-progress :percentage="processNum" :stroke-width="1" status="exception"></el-progress>
                </div>
              </FormItem>
              <FormItem>
                <div style="line-height:21px; color:#fff;">
                  <p style="marginBottom:5px">文件的命名规范如下：</p>
                  <span>1.使用 UTF-8 编码；</span><br/>
                  <span>2.区分大小写；</span><br/>
                  <span>3.长度必须在 1-100 字节之间；</span><br/>
                  <span> 4.不能以
                    <span style="color:red">/</span> 或者
                    <span style="color:red">\</span>字符开头。
                  </span>
                </div>
              </FormItem>
            </Form>
          </Col>
        </Row>
      </div>
      <div slot="footer"></div>
      <Spin size="large" fix v-if="spinShow">上传中...</Spin>
    </Modal>

    <Modal
      title="视频播放"
      :mask-closable="false"
      v-model="videoModal"
      :closable="false"
    >
      <div id="file_Player">
        <suma-player ref="videoPlayer" @plyerEnd="plyerEnd"></suma-player>
      </div>
      <p slot="footer">
        <Button style="background:rgba(13,67,111,1);color:rgba(199,219,244,1)" @click="playCancel">关闭</Button>
      </p>
    </Modal>

    <Modal
      v-model="videoChildModal"
    >
      <p slot="header">
        下载列表
      </p>
      <Table highlight-row ref="currentRowTable" :columns="videoColumns" :data="videoData"></Table>
      <p slot="footer">
        <Button style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);" @click="downloadShut">关闭</Button>
      </p>
    </Modal>
    <player ref="player" @isBaseCreate="videoCreateSuccess" @isEnd="playEnding"/>  
  </div>
</template>

<script>
import api from '@/api/api'
import Vue from 'vue'
import Cookies from 'js-cookie'
import { Upload, Progress, Row, Switch } from 'element-ui'
import axios from 'axios'
import sumaPlayer from "@/components/SumaPlayer.vue";
import player from './player.vue'
Vue.use(Upload)
Vue.use(Progress)
export default {
  components:{
    'suma-player':sumaPlayer,
    player
  },
  data () {
    return {
      videoChildModal:false,
      videoModal:false,
      successProgress:false,
      uploadModal:false,
      spinShow:false,
      errorProgress:false,
      loadingStatus:false,
      fileType: 10000, // 文件管理：按文件类型查询
      fileName:'',
      fileList: [],
      // 文件管理表格数据
      columns1: [
        {
          title: '文件名称',
          key: 'fileName',
          minWidth:160
        },
        {
          title: '通道',
          key: 'channelName',
          minWidth:120
        },
        {
          title: '类别',
          key: 'type',
          minWidth:120
        },
        {
          title: '文件格式',
          key: 'fileFormat',
          minWidth:100
        },
        {
          title: '状态',
          key: 'status',
          minWidth:160,
          maxWidth:180,
        },
        {
          title: '文件大小',
          key: 'fileSize',
          minWidth:120
        },
        {
          title: '创建时间',
          key: 'createAt',
          minWidth:170
        },
        {
          title: '操作',
          key: 'handle',
          align: 'center',
          fixed:'right',
          minWidth:160
        }
      ],
      //================播放
      playIndex:0,
      //=================上传
      uploadFileData:null,
      shardList:[],
      fileIndex:1,//文件从第一块开始
      processNum:0,
      fileId:'',
      isUploadFlag:false,//是否上传中
      //===================下载
      videoColumns:[
        {
          title: '文件名',
          key: 'fileName'
        },
        {
          title: '操作',
          key: 'handle'
        },
      ],
      videoData:[{
        fileName:'文件'
      }],
      fileData: [],
      pageNo:1, 
      pageSize:10,
      totalCount:0,
      isGray:false, // 根据操作员登录时拿到的operationPermissions的值（默认为false），来判断该操作员是否拥有相应的权限，若为false，则相应的功能置灰。
      videoIndex:0,//播放器索引
      videoGroupIndex:0,
      timer:null,
      base:1,
      tabHeight:0
    }   
  },
  created(){
      this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true;
      this.fileList = JSON.parse(localStorage.getItem('selectAllData'))[1].metaDictList
      this.fileList.unshift({metaDataValue:10000,metaDataName:'全部'})
      this.selectUserChannel()
      this.init()
      this.$bus.$on('isBaseCreate',(obj)=>{
        this.videoCreateSuccess(obj)
      })
  },
  mounted(){
    let vm = this;
    this.getDom();
    window.onresize = function(){
      vm.getDom();
      /**播放窗口位置错位问题 */
      let odiv = document.getElementById('file_Player')
      let allWidth = odiv.offsetWidth
      let allHeight = odiv.offsetHeight
      let x = odiv.getBoundingClientRect().left
      let y = odiv.getBoundingClientRect().top
      let rows = 1
      let columns = 1
      let groupIndex = vm.videoGroupIndex
      let param = {
          groupIndex,x,y,width:allWidth,height:allHeight,rows,columns
      }
      vm.$refs.player.adjustQmlPlayer(param)
    }
  },
  beforeDestroy(){
    clearInterval(this.timer);
    this.$bus.$off('isBaseCreate')
  },
  methods:{

    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      this.tabHeight  = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },

    downloadShut(){
      this.videoChildModal = false
    },
      
    init(){
      let vm = this
      this.columns1.forEach(item => {
        if (item.key === 'handle') {
          item.render = (h, param) => {
            return h('p',{
              style:{
                display:'flex'
              }
            } ,
            [
              h(
                'span',
                {
                  style:{
                    marginRight:'5px',
                    color:!this.isGray?'#02E8FF':'gray',
                    cursor:!this.isGray?'pointer':'not-allowed',
                    fontSize:'14px',
                    display:vm.showDownLoadBtn(param.row)
                  },
                  on: {
                    click:() => {
                      if(!this.isGray){
                        this.download(param.row)
                      }
                    }
                  }
                },
                "下载"
              ),
              h(
                'Poptip',
                {
                  props:{
                    confirm:true,
                    title:"您确定要删除这条数据吗？",
                    transfer:true
                  },
                  on:{
                    'on-ok':() => {
                      vm.removeByID(param.row)
                    }
                  }
                },
                [
                  h(
                    'span',
                      {
                        style:{
                          display:vm.showRemoveBtn(param.row),
                          marginRight:'5px',
                          color:'#02E8FF',
                          cursor:'pointer',
                          fontSize:'14px',
                        }
                      },
                    "删除"
                  )
                ]
              ),
              h(
                'span',
                {
                  style:{
                    display:vm.showRemoveBtn1(param.row),
                    marginRight:'5px',
                    color:'gray',
                    cursor:'not-allowed',
                    fontSize:'14px',
                  }
                },
                '删除'
              ),
              h(
                'span',
                {
                  style:{
                    marginRight:'5px',
                    color:'#02E8FF',
                    cursor:'pointer',
                    fontSize:'14px',
                    display:vm.showPlayBtn(param.row)//导出完成并注入完成
                  },
                  on: {
                    click:() => {
                      this.playVideo(param.row)
                    }
                  }
                },
                "播放"
              ),
            ])
          }
        } else if (item.key === 'status') {
          item.render = (h,param) => {
            return h('div',{
              style:{
                display: 'flex',
                'align-items':'center'
              }
            },
            [ 
              h(
                'div',
                {
                  style: {
                    display:param.row.fileState ===3|| (param.row.fileCategory!=3 && param.row.fileState===2 ||param.row.fileCategory!=3 && param.row.fileState===4 &&param.row.injectStatus !==3)?'':'none',
                    width:'8px',
                    height:'8px',
                    'margin-right':'3px',
                    'border-radius':'50%',
                    background:param.row.fileState ===3?'#F56C6C':'#02E8FF',
                  }
                }
              ),
              h(
                'span',
                {
                  style:{
                    color:param.row.fileState ===3?'#F56C6C':(param.row.fileCategory!=3 && param.row.fileState===2 ||param.row.fileCategory!=3 && param.row.fileState===4 &&param.row.injectStatus !==3)?'#02E8FF':'',
                    'margin-right':'15px'
                  }
                },
                param.row.status
              ),
              h(
                'i-circle',
                {
                  props:{
                    percent:param.row.fileState===4?100:param.row.exportRate,
                    size:35,
                    'stroke-color':'#02E8FF',
                    //  'stroke-width':2,
                  },
                  style:{
                    fontSize:'10px',
                    display:vm.showPressCircle(param.row),
                    verticalAlign:'middle',
                    color:'#C8DCF4',
                  }
                }, vm.showPercent(param.row)
              )
            ])
          }
        } else if (item.key === 'fileSize') {
          item.render = (h,param)=>{
              return h(
                        'span',
                        {},
                        // param.row.fileSize+" MB"
                        (param.row.fileSize == 0) ? '' : param.row.fileSize+" MB"
                      )
          }
        }
      })
      this.videoColumns.forEach(item=>{
        if(item.key === 'handle'){
          item.render = (h,param)=>{
            return h('div',[
              h('span',{
                style:{
                  color:'#02E8FF',
                  cursor:'pointer',
                },
                on: {
                  click:() => {
                    this.downloadOne(param.row)
                  }
                }
              },'下载')
            ])
          }
        }
      })
    },
    //显示下载按钮
    showDownLoadBtn(row){
      /**上传的情况（有权限情况下） */
      if(row.fileCategory === 3){
        if(row.fileState == 4){
          return 'block'
        }else{
          return 'none'
        }
      }else{
        /**导出文件的状态 */
        if(row.fileState == 0||row.fileState == 1||row.fileState == 2||row.fileState == 3){
          return 'none'
        }else{
          if(row.fileState == 4){
            if(row.injectStatus === 3){
              return 'block'
            }else{
              return 'none'
            }
          }
        }
      }
    },
    /**显示删除按钮 */
    showRemoveBtn(row){
      /**上传的情况（有权限情况下） */
      if(!this.isGray){
        if(row.fileCategory === 3){
          if(row.fileState == 0){
            return 'none'
          }else{
            return 'block'
          }
        }else{
          /**导出文件的状态 */
          if(row.fileState == 0||row.fileState == 2){
            return 'none'
          }else{
            if(row.fileState == 4){
              if(row.injectStatus === 3 || row.injectStatus === 2){
                return 'block'
              }else{
                return 'none'
              }
            }
          }
        }
      }else{
        return 'none'
      }
    },
      
    /**显示删除按钮 */
    showRemoveBtn1(row){
      /**上传的情况 (没有权限情况下)*/
      if(this.isGray){
        if(row.fileCategory === 3){
          if(row.fileState == 0){
            return 'none'
          }else{
            return 'block'
          }
        }else{
          /**导出文件的状态 */
          if(row.fileState == 0||row.fileState == 2){
            return 'none'
          }else{
            if(row.fileState == 4){
              if(row.injectStatus === 3 || row.injectStatus === 2){
                return 'block'
              }else{
                return 'none'
              }
            }
          }
        }
      }else{
        return 'none'
      }
    },
      
    //是否显示播放按钮
    showPlayBtn(row){
    //  pram.row.fileFormat === 'TS'&&param.row.fileState === 4&&param.row.injectStatus === 3?'':'none'
      let str = row.fileFormat.toLowerCase()
      if(str!=='ts' && str!=='mp4' && str!=="3gp" && str!=='flv' && str!=="avi" && str!=="mov" && str!=="rmvb" && str!=="rm"){
        return 'none'
      }
      if(row.fileCategory===3){
        if(row.fileState === 4){
          return 'block'
        }else{
          return 'none'
        }
      }else{
        if(row.fileState === 4&&row.injectStatus === 3){
            return 'block'
        }else{
            return 'none'
        }
      }
    },
    
    //显示进度环
    showPressCircle(row){
    //   param.row.fileState === 2||(param.row.fileState===4&&param.row.injectStatus!==3)?'':'none'
      if(row.fileCategory ===3){
        /**文件上传 */
        return 'none'
      }else{
        /**文件导出 */
        if(row.fileState === 2||(row.fileState===4 && row.injectStatus!==3 && row.injectStatus!==2)){
          return 'block'
        } else {
          return 'none'
        }
      }
    },
      
    /**显示百分数 */
    showPercent(row){
      if(row.fileState===4){
        return '100%'
      }else{
        return row.exportRate?row.exportRate+'%':''
      }
    },

    search(){
      this.pageNo = 1
      this.selectUserChannel()
    },
      
    //文件查询
    selectUserChannel(){
      let param = {
        fileName:this.fileName,
        fileCategory:this.fileType === 10000?'':this.fileType,
        pageNo:this.pageNo,
        pageSize:this.pageSize
      }
      this.loadingStatus = true
      api.searchAccountFileList(param).then(res=>{
        console.log('文件列表',res)
        if(res.result){
          if(!res.result.length && this.pageNo>1){
            this.pageNo--
            this.selectUserChannel()
            return false
          }
          for(let item of res.result){
            item.type = item.fileCategory === 0?'视频+音频':item.fileCategory ===1?'纯视频':item.fileCategory ===2?'纯音频':'文件上传'
            //文件上传
            if(item.fileCategory ===3){
              /**文件上传 */
              if(item.fileState === 0){
                item.status = '正在上传'
              }else if(item.fileState === 1){
                item.status = '注入中'
              }else if(item.fileState === 3){
                item.status = '生成失败'
              }else if(item.fileState===4){
                item.status = '已完成'
              }
            }else{
              /**文件导出 */
              if(item.fileState === 0){
                item.status = '正在上传'
              }else if(item.fileState === 1){
                item.status = '注入中'
              }else if(item.fileState === 2){
                item.status = '正在导出'
              }else if(item.fileState === 3){
                item.status = '生成失败'
              }else if(item.fileState===4){
                if(item.injectStatus===3){
                  item.status = '已完成'
                }else if(item.injectStatus === 2){
                  item.status = '生成失败'
                }else{
                  item.status = '正在导出'
                }
              }
            }
          }
          this.fileData = res.result
          this.totalCount = res.count
          for(let data of res.result){
            if(data.status === '正在导出'){
              clearInterval(this.timer)
              this.timer = setInterval(()=>{
                this.selectUserChannel_1()
              },5000)
              break
            }
          }
        }
        this.loadingStatus = false
      },err=>{
        this.loadingStatus = false
      })
    },
      
    //文件查询
    selectUserChannel_1(){
      let param = {
        fileName:this.fileName,
        fileCategory:this.fileType === 10000?'':this.fileType,
        pageNo:this.pageNo,
        pageSize:this.pageSize
      }
      api.searchAccountFileList(param).then(res=>{
        console.log('文件列表',res)
        if(res.result){
          for(let item of res.result){
            item.type = item.fileCategory === 0?'视频+音频':item.fileCategory ===1?'纯视频':item.fileCategory ===2?'纯音频':'文件上传'
            //文件上传
            if(item.fileCategory ===3){
              /**文件上传 */
              if(item.fileState === 0){
                item.status = '正在上传'
              }else if(item.fileState === 1){
                item.status = '注入中'
              }else if(item.fileState === 3){
                  item.status = '生成失败'
              }else if(item.fileState===4){
                  item.status = '已完成'
              }
            }else{
              /**文件导出 */
              if(item.fileState === 0){
                item.status = '正在上传'
              }else if(item.fileState === 1){
                item.status = '注入中'
              }else if(item.fileState === 2){
                item.status = '正在导出'
              }else if(item.fileState === 3){
                item.status = '生成失败'
              }else if(item.fileState===4){
                if(item.injectStatus===3){
                  item.status = '已完成'
                }else if(item.injectStatus === 2){
                  item.status = '生成失败'
                }else{
                  item.status = '正在导出'
                }
              }
            }
          }
          this.fileData = res.result
          this.totalCount = res.count
          /**查看是不是现在没有导出状态数据 */
          let flag = true
          for(let data of this.fileData){
            if(data.status=="正在导出"){
              flag = false
              break
            }
          }
          if(flag){
            clearInterval(this.timer)
          }
        }
      })
    },
      
    //文件删除
    removeByID(row){
      console.log('文件',row)
      let param = {
        fileId:row.fileId
      }
      api.deleteFile(param).then(res=>{
        this.$Message.success('文件删除成功')
        this.selectUserChannel()
      })
    },

    //下载
    download(row){
      console.log(row)
      if(row.fileNum>1){
        this.showVideoChild(row)
        return false
      }
      let downloadLink = document.createElement('a')
      downloadLink.href = row.downUrl
      downloadLink.download = row.fileName
      // 触发点击事件执行下载
      downloadLink.click()
      if (row.fileName) {
        let param = {
          fileName:row.fileName,
        }

        api.saveDownloadLog(param).then(
          res => {
            // this.$Message.success('下载成功')
          }
        )
      }
    },

    downloadOne(row){
      let downloadLink = document.createElement('a')
      downloadLink.href = row.downUrl
      downloadLink.download = row.fileName
      // 触发点击事件执行下载
      downloadLink.click()
      if (row.fileName) {
        let param = {
          fileName:row.fileName,
        }

        api.saveDownloadLog(param).then(
          res => {
            // this.$Message.success('下载成功')
          }
        )
      }
    },

    showVideoChild(row){
      console.log(row)
      this.videoChildModal = true
      let param = {
        fileId:row.fileId
      }
      api.getFileDwonUrlList(param).then(res=>{
        console.log(res.result)
        this.videoData = res.result
      })
    },
      
    // 文件管理：播放视频
    playVideo(row){
      this.videoModal = true
      this.playIndex = 0
      //上传
      if(row.fileCategory ===3){
        if(row.fileFormat.toLowerCase()==='ts'){
          this.playUrl = row.playUrl
        }else{
          this.playUrl = [row.downUrl]
        }
      }else{
        this.playUrl = row.playUrl
      }
      //==============替换播放器
      if(this.$sumaQtPlayer){
        //qt播放
        let param = {
          elementClassName:'',
          elementId:'',
          mode:'base'
        }
        this.$refs.player.replaceVideoWithPlayer1(param)
      }else{
        this.$refs.videoPlayer.playVodWithHlsUrl(this.playUrl[this.playIndex],true)
      }
    },

    //创建成功
    videoCreateSuccess(obj){
      //创建替换播放器的索引
      this.videoIndex = obj.playerIndex
      this.videoGroupIndex = obj.groupIndex
      this.playIndex = 0
      this.playInit()
    },

    //播放结束
    playEnding(index){
      //判断是不是一个播放器
      if(index === this.videoGroupIndex){
        if(this.playIndex === this.playUrl.length-1){
          this.$Message.info('播放结束')
          return false
        }
        this.playIndex++
        this.playInit()
      }
    },
        
    //初始化播放流
    playInit(){
      let param = [{
        "x": 200,
        "y": 100,
        playerIndex:this.videoIndex,   //playerIndex不为-1，则优先使用playerIndex初始化
        groupIndex:this.videoGroupIndex,
        playerInfo: {
          channelId: "",
          channelName: "",
          channelUrl:this.playUrl[this.playIndex],
        }
      }]   
      this.$refs.player.prepareQmlPlayer2(param)
    },

    plyerEnd(){
      if(this.playIndex === this.playUrl.length-1){
        this.$Message.info('播放结束')
        return false
      }
      this.playIndex++
      this.$refs.videoPlayer.playVodWithHlsUrl(this.playUrl[this.playIndex],true)
    },

    playCancel(){
      if(this.$sumaQtPlayer){
        this.videoModal = false
        this.$refs.player.destroyQmlPlayer([{groupIndex:-2,playerIndex:-2}])
      }else{
        this.videoModal = false
        this.$refs.videoPlayer.pause()
      } 
    },
      
    //================================
    uploadFile(){
      this.uploadModal = true
      this.successProgress = false
      this.errorProgress = false
      this.isUploadFlag = false
      this.processNum = 0
      this.uploadFileData = null
    },
    
    //获取字符串的字节大小
    GetCharLength(str) {
      var iLength = 0 //记录字符的字节数
      for (var i = 0;i < str.length;i++) { //遍历字符串中的每个字符
        if (str.charCodeAt(i) > 255) {
        //如果当前字符的编码大于255
          iLength += 2 //所占字节数加2
        } else {
          iLength += 1 //否则所占字节数加1
        }
      }
      return iLength //返回字符所占字节数是否大于100
    },

    beforeUpload(file){
      let str = this.GetCharLength(file.name)
      if (100 < str || str < 1) {
        this.$Message.info('文件名称长度必须在1-100字节之间')
        return false
      }
      if(this.isUploadFlag){
          //是否正在上传中
          this.$Message.info('正在上传中,请稍后')
          return false
      }
      this.successProgress = false
      this.errorProgress = false
      this.isUploadFlag = false
      this.processNum = 0
      this.uploadFileData = file
      this.checkFileNameIsExisted(file)
      return false
    },

    //查询文件是否存在
    checkFileNameIsExisted(file){
      let param = {
          fileName:file.name
      }
      api.checkFileNameIsExisted(param).then(res=>{
        console.log(res)
        if(res.result){
          //已存在
          this.$Message.info('文件已上传请重新命名')
        }else{
          this.successProgress = true
          this.shardList = []
          let size = file.size
          let shardSize = 100 * 1024 * 1024
          this.shardCount = Math.ceil(size / shardSize) //总片数
          let index = 1
          for (var i = 0; i < this.shardCount; ++i) {
            //计算每一片的起始与结束位置
            let start = i * shardSize
            let end = Math.min(size, start + shardSize)
            this.shardList.push(file.slice(start, end))
          }
          this.upload(this.shardCount,index,'')
        }
      })
    },
      
    upload(shardCount,index,fileId){
      var jsonText = {
        versionName: '4.1.8',
        versionCode: '340',
        operatorId: Cookies.get('operatorId'),
        operatorGroup: Cookies.get('operatorGroup'),
        info: {
          userId: JSON.parse(localStorage['loginRes']).user.userId,
          fileName: this.uploadFileData.name,
          chunkNumber: index,
          totalChunks: shardCount,
          fileId:fileId,
          fileSize:this.uploadFileData.size
        }
      }
      console.log('文件',this.shardList[index - 1])
      let param = new FormData()
      param.append('jsonText', JSON.stringify(jsonText))
      param.append('file', this.shardList[index - 1])
      let token = Cookies.get('token')
      let instance = axios.create({
        baseURL: './api/drbs',
        timeout: 120000,
        headers: {
            'Content-type': 'multipart/form-data',accessToken: `${token}`
        }
      })
      this.isUploadFlag = true
      instance.post('/cmc/general/uploadPartMergeFile', param).then(res=>{
        if (res.data.status == 0) {
          if(index === this.shardCount){
            this.processNum = parseInt(index / this.shardCount * 100)
            this.selectUserChannel()
            this.isUploadFlag = false
            return false
          }
          this.processNum = parseInt(index /this.shardCount * 100)
          this.fileId = res.data.data.result
          index++
          this.upload(this.shardCount,index,this.fileId)
        }else{
          this.isUploadFlag = false
          this.successProgress = false
          this.errorProgress = true
        }
      },err=>{
        this.isUploadFlag = false
        this.successProgress = false
        this.errorProgress = true
      })
    },
      
    //取消上传
    cancelUpload(){
      let param = {
        userId: JSON.parse(localStorage['loginRes']).user.userId,
        fileId:this.fileId,
        fileName: this.uploadFileData.name,
      }
      api.cancelUploadPartMergeFile(param).then(res=>{
        this.$Message.info('取消上传')
        this.isUploadFlag = false
        this.successProgress = false
        this.processNum = 0
      })
    },
    //=====================分页
    pageChange(page){
      this.pageNo = page
      this.selectUserChannel()
    },

    sizeChange(size){
      this.pageSize = size
      if(this.pageNo ===1){
        this.selectUserChannel()
      }
    }
  }
}
</script>
