  #uploadModal .el-upload-dragger{
      background: #0D436F
  }
  #uploadModal .el-progress__text{
    color: #fff;
  }

  #fileTable .ivu-table-fixed-right-header {
    /* display: none; */
    /* background:rgba(41,100,148,1); */
    background-color:rgba(41,100,148,1);
    border-bottom:none;
  }
</style>